
import { IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonGrid, IonCard, IonRow, IonCol } from '@ionic/vue';
import { defineComponent } from 'vue';
import CardComponent from '@/components/CardComponent.vue';
import BarChart from '@/components/BarChart.vue';
import TableCard from '@/components/TableCard.vue';
import DonutChart from '@/components/DonutChart.vue';
import PieChart from '@/components/PieChart.vue';
import ShortcutCard from '@/components/ShortcutCard.vue';
import axios from 'axios';
import qs from 'qs';


export default defineComponent({
  name: "DefaultPage",
  data(){
    return {
      labels: [
      "ID", "Name"
      ],
      tableData: [
        ["1", "Alex"],
        ["2", "Matej"],
        ["3", "Martin"],
        ["4", "John"],
        ["5", "Michael"],
        ["6", "Elias"],
        ["7", "Johann"],
      ],
data: {
     labels: ['VueJs', 'EmberJs', 'ReactJs', 'AngularJs'],
    datasets: [
      {
        backgroundColor: ['#41B883', '#E46651', '#00D8FF', '#DD1B16'],
        label: "Verkufe",
        data: [990, 20, 80, 10],
        borderWidth: [0, 0, 0, 0],
      }
    ],
},
   
  
options: {
    responsive: true,
   //maintainAspectRatio: false
  },
isBookmark: false
    }
  },
  components: {
    CardComponent,
    BarChart,
    DonutChart,
    PieChart,
    TableCard,
    IonGrid,
   // IonHeader,
   //IonToolbar,
    //IonTitle,
    IonCol,
    IonRow,
    IonCard,
    ShortcutCard
  },
  props: {
    title: String
  },
  mounted(){
    axios.post('https://alex.polan.sk/control-center/bookmarks.php?' + qs.stringify({location: window.location.href, checkBookmark: "checkBookmark"}))
    .then(response => {
      this.isBookmark = response.data;
    })
  },
  methods: {
    toggleBookmark(){
      if(this.isBookmark){
        this.isBookmark = false;
        this.$emit('newData');
        axios.post("https://alex.polan.sk/control-center/bookmarks.php?deleteBookmark=deleteBookmark&location=" + window.location.href);
      }else{
        this.isBookmark = true;
        this.$emit('newData');
        axios.post("https://alex.polan.sk/control-center/bookmarks.php?newBookmark=newBookmark&title=" + this.title + "&location=" + window.location.href);
      }
    }
  }
});
